/* ======================
   1. RESET & BASE STYLES
   ====================== */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  overflow-y: scroll;
}

@import url('https://cdn.rawgit.com/moonspam/NanumSquare/master/nanumsquare.css');

body {
font-family: "Pretendard Variable", Pretendard, -apple-system, BlinkMacSystemFont, system-ui, Roboto, "Helvetica Neue", "Segoe UI", "Apple SD Gothic Neo", "Noto Sans KR", "Malgun Gothic", "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", sans-serif;
}

h1, h2, h3 {
  letter-spacing: -1px;
}

span, p {
  letter-spacing: -0.7px;
}

button {
  background: none;
  border: none;
  cursor: pointer;
  color: none;
}

img {
  display: block;
  max-width: 100%;
}

/* ======================
   2. LAYOUT
   ====================== */
.app-container {
  max-width: 600px;
  margin: 0 auto;
  min-height: 100vh;
}

.page-content {
  padding-bottom: 80px;
}

/* ======================
   3. HEADER
   ====================== */
.header {
  background-color: white;
  padding: 8px 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: sticky;
  top: 0;
  z-index: 20;
  gap: 20px;
}

.header-title {
  flex: 1;
  text-align: center;
  
}

.header-title h2{
  font-size: 16px;
}


.header-search-bar {
  position: relative;
  flex: 1;
  padding: 12px 48px 12px 16px;
  background-color: #f3f4f6;
  border-radius: 8px;
  font-size: 14px;

}

.header-search-bar input {
  width: 100%;
  outline: none;
  border: none;
  background: none;
  transition: box-shadow 0.2s;
}

.header-search-bar input:focus {
  box-shadow: 0 0 0 2px #3b82f6;
}

.header-search-bar input::placeholder {
  color: #9ca3af;
}

.header-actions {
  display: flex;
  align-items: center;
  gap: 12px;
}

.header-btn {
  font-size: 14px;;
  font-weight: 500;
  background: none;
  border: none;
  cursor: pointer;
}

.back-btn {
  background: none;
  border: none;
  font-size: 16px;
  cursor: pointer;
  padding: 8px;
}

/* ======================
   4. BOTTOM NAVIGATION
   ====================== */
.bottom-nav {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: white;
  border-top: 1px solid #e5e7eb;
  z-index: 1000;
}

.bottom-nav-content {
  display: flex;
  justify-content: space-around;
  padding: 12px 0;
  margin: 0 auto;
}

.nav-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: none;
  border: none;
  cursor: pointer;
  color: #9ca3af;
}

.nav-item.active {
  color: #ec4899;
}

.nav-icon {
  font-size: 24px;
}

.nav-label {
  font-size: 12px;
}

/* ======================
   5. BUTTONS (공통)
   ====================== */
.more-btn {
  font-size: 15px;
  color: #6b7280;
  display: flex;
  align-items: center;
  background: none;
  border: none;
  cursor: pointer;
}

.close-btn {
  background: none;
  border: none;
  padding: 8px;
  cursor: pointer;
  border-radius: 50%;
  transition: background-color 0.2s;
  color: #6b7280;
}

.close-btn:hover {
  background-color: #f3f4f6;
}

.favorite-btn {
  background: none;
  border: none;
  padding: 8px;
  cursor: pointer;
  border-radius: 50%;
  transition: all 0.2s;
  color: #d1d5db;
}

.favorite-btn:hover {
  background-color: #f3f4f6;
  color: #ef4444;
}

/* ======================
   6. TABS (공통)
   ====================== */
.tabs,
.alarm-tabs,
.event-tabs {
  display: flex;
  border-bottom: 1px solid #e5e7eb;
  margin-bottom: 16px;
}

.tab-btn,
.alarm-tab-btn {
  flex: 1;
  padding: 12px 0;
  text-align: center;
  background: none;
  border: none;
  cursor: pointer;
  color: #9ca3af;
  font-size: 16px;
}

.tab-btn.active,
.alarm-tab-btn.active {
  font-weight: bold;
  color: #FF552E;
  border-bottom: 2px solid #FF552E;
}

/* 바텀시트용 탭 */
.tab {
  padding: 8px 24px;
  border-radius: 20px;
  border: none;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
  background-color: #f3f4f6;
  color: #6b7280;
}

.tab:hover {
  background-color: #e5e7eb;
}

.tab.active {
  background-color: #111827;
  color: white;
}

/* ======================
   7. SEARCH BAR (공통)
   ====================== */
.search-bar {
  position: relative;
  /* margin-bottom: 16px; */
}

.search-bar input {
  width: 100%;
  padding: 12px 48px 12px 16px;
  background-color: #f3f4f6;
  border: none;
  border-radius: 12px;
  font-size: 14px;
  outline: none;
  transition: box-shadow 0.2s;
}

.search-bar input:focus {
  box-shadow: 0 0 0 2px #3b82f6;
}

.search-bar input::placeholder {
  color: #9ca3af;
}

.search-icon {
  position: absolute;
  right: 12px;
  top: 50%;
  transform: translateY(-50%);
  background: none;
  border: none;
  padding: 4px;
  cursor: pointer;
  color: #9ca3af;
}

/* ======================
   8. CARDS & ITEMS (공통)
   ====================== */
/* 케이크 아이템 */
.cake-item {
  display: flex;
  align-items: center;
  flex-direction: column;
  border-radius: 8px;
  cursor: pointer;
  gap: 10px;
}

.row{
   flex-direction: row !important;
}

.cakeItem-warp{
  display: flex;
  gap: 10px;
  overflow: hidden;
  overflow-x: auto;
}
.cakeItem-warp::-webkit-scrollbar {
  display: none; /* Chrome/Safari */
}





.cta_btn{
 height: 54px;
 background-color: #FF552E; 
}


.cake-image {
  width: 130px;
  height: 130px;
  background-color: #FFD8CF;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 32px;
  border-radius: 100%;
}

.cake-image img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  border-radius: 8px;
}

.cake-info {
  display: flex;
  flex-direction: column;
  gap: 4px;
}


.badge p{
  display: flex;
  border: 1px solid #FF8A65;
  color: #FF8A65;
  padding: 2px 4px;

}

.badge img{
  width: 24px;
  height: 24px;
}


.badge{
  display: flex;
  justify-content: space-between;
  align-items: center;
}


.cake-name {
  font-size: 14px;
  font-weight: 500;
}


.cake-discount{
  font-size: 14px;
  font-weight: 500;
  color: #868793;
  text-decoration: line-through;
  
}

.flex{
  display: flex;
  /* justify-content: space-between; */
  align-items: center;
  gap: 4px;
}


.space-between{
  display: flex;
  justify-content: space-between;
}

.explanation{
  font-size: 12px;
  color: #5F616D;
  font-weight: 300;
  
}


.sale{
   font-size: 12px;
   background-color: #FF4757;
   padding: 2px 6px;
   display: flex;
   justify-content: center;
   align-items: center;
   color: #fff;
   font-weight: 300;
}


.best{
   font-size: 12px;
   background-color: #FFA500;
   padding: 2px 6px;
   display: flex;
   justify-content: center;
   align-items: center;
   color: #fff;
   font-weight: 300;
}

.new{
   font-size: 12px;
   background-color: #FF6B35;
   padding: 2px 6px;
   display: flex;
   justify-content: center;
   align-items: center;
   color: #fff;
   font-weight: 300;
}
.limitend{
   font-size: 12px;
   background-color: #7B68EE;
   padding: 2px 6px;
   display: flex;
   justify-content: center;
   align-items: center;
   color: #fff;
   font-weight: 300;
}





.cake-price {
  font-size: 16px;
  font-weight: bold;
}

/* 케이크 그리드 카드 */
.cake-grid {
display: flex ;
flex-direction: column;
  gap: 10px;
  padding: 0 16px;
  overflow: hidden;
  margin-bottom: 50px;
}

.cake-card {
  display: flex;
  background-color: white;
  overflow: hidden;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
}

@media screen and  (max-width: 600px) {
  .cake-grid{
    grid-template-columns: repeat(2, 1fr);
  }
}

.cake-card-image {
  width: 130px;
  height: 130px;
  background-color: #E9E9E9;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 48px;
  border-radius: 100%;
}

.cake-card-image img {
  width: 100%;
  height: 90%;
  object-fit: contain;
}

.cake-card-content {
  width: 100%;
  font-size: 16px;
  font-weight: 600;
  padding: 12px;
  /* border: 1px solid #E9E9E9; */
}

/* ======================
   9. SECTIONS
   ====================== */
.cake-section {
  padding: 16px 0 0 16px ;
}

.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.section-title {
  font-size: 24px;
  font-weight: bold;
  /* padding: 16px; */
}

/* ======================
   10. USER INFO
   ====================== */
.user-welcome {
  padding: 12px 16px;
}

.welcome-text {
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 8px;
}

.gold-card {
  background: #FF552E;
  color: white;
  border-radius: 5px 20px 5px 5px;
  padding: 20px 12px;
}

.gold-title{
  display: flex;
  gap: 10px;
  align-items: center;
}

.gold-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}

.gold-progress-bg {
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 9999px;
  height: 8px;
}

.gold-progress-bar {
  background-color: white;
  border-radius: 9999px;
  height: 8px;
  width: 100%;
}

/* ======================
   11. STATS
   ====================== */
.stats-container {
  display: flex;
  justify-content: space-around;
  padding: 8px 0;
  border-bottom: 1px solid #e5e7eb;
}

.stat-item {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4px
}

.stat-icon {
  width: 20px;
  height: 20px;
  /* margin-bottom: 4px; */
}
.stat-icon img{
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.stat-label {
  font-size: 12px;
  color: #6b7280;
}

.stat-value {
  font-weight: bold;
}

/* ======================
   12. BANNERS
   ====================== */
.anniversary-banner {
  overflow: hidden;
  border-radius: 24px;
  margin: 0 16px;
  width: calc(100% - 32px);
}

.anniversary-banner img {
  width: 100%;
  object-fit: cover;
}

.coupon-event {
  background: url(./assets/images/mini_banner.png) no-repeat center/contain;
  width: 100%;
  max-width: 600px;
  aspect-ratio: 375 / 126; /* 배너의 원본 비율 */
}

/* ======================
   13. DETAIL PAGE
   ====================== */
.detail-image {
  height: 400px;
  background-color: #f3f4f6;
  display: flex;
  align-items: center;
  justify-content: center;
}

.detail-image img {
  height: auto;
  object-fit: contain;
}

.detail-info {
  padding: 16px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.detail-name {
  font-size: 22px;
  font-weight: 900;
  color: #FF552E;
}

.detail-description {
  color: #6b7280;
  line-height: 1.6;
}

.detail-price {
  font-size: 24px;
  font-weight: 900;
}

.recommendedCakes {
  padding: 0 16px;
}

.recommendedCakes h3 {
  padding-bottom: 10px;
}

/* ======================
   14. QUANTITY CONTROL
   ====================== */
.quantity-control {
  padding: 12px 16px;
  border-bottom: 1px solid #e5e7eb;
}

.quantity-content {
  display: flex;
  gap: 12px;
  margin: 0 auto;
}

.quantity-selector {
  display: flex;
  align-items: center;
  border-radius: 8px;
}

.quantity-btn {
  background: none;
  border: none;
  cursor: pointer;
}

.quantity-value {
  padding: 0 16px;
  font-weight: bold;
}

.total-price {
  flex: 1;
  text-align: right;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.total-amount {
  font-size: 24px;
  font-weight: bold;
}

/* ======================
   15. ACTION BUTTONS
   ====================== */
.action-buttons {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: white;
  padding: 0 16px;
  z-index: 1000;
}

.action-buttons-content {
  display: flex;
  gap: 12px;
  margin: 0 auto;
  padding: 12px 0;
}

.btn-cart {
  flex: 1;
  border: 1px solid #B6B7BE;
  background-color: #F5F5F5;
  border-radius: 8px;
  padding: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  gap: 6px;
  font-size: 16px;
}

.btn-order {
  flex: 2;
  background-color: #333333;
  color: white;
  border: none;
  border-radius: 8px;
  padding: 16px;
  cursor: pointer;
  font-size: 16px;
  font-weight: 500;
}

.detail-caution{
  display: flex;
  align-items: center;
}

/* ======================
   16. POINTS PAGE
   ====================== */
.points-card {
  margin: 16px;
  border-radius: 12px;
  border: 1px solid #e4e4e4;
  padding: 12px;
}

.stamp-grid {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 8px;
  margin-bottom: 16px;
}

.stamp-item {
  aspect-ratio: 1;
  border-radius: 50%;
  border: 4px solid #e5e7eb;
  display: flex;
  align-items: center;
  justify-content: center;
}

.stamp-item.active {
  border-color: #FF552E;
}

.stamp-text {
  text-align: center;
  font-size: 16px;
  font-weight: bold;
  color: #cacbcf;
}

.stamp-text.active {
  color: #FF552E;
  font-weight: 800;
}

.point-history {
  padding: 0 16px;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.point-item {
  background-color: white;
  padding: 16px;
  border-radius: 8px;
}

.point-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 4px;
}

.point-date {
  font-size: 14px;
  color: #6b7280;
}

.point-time {
  font-size: 12px;
  color: #9ca3af;
}

.point-amount {
  font-size: 20px;
  font-weight: bold;
}

.point-amount.positive {
  color: #dc2626;
}

.point-amount.negative {
  color: #2563eb;
}

.point-description {
  text-align: right;
  font-size: 14px;
  color: #374151;
}

.point-note {
  text-align: right;
  font-size: 12px;
  color: #3b82f6;
  margin-top: 4px;
}

/* ======================
   17. BOTTOM SHEET
   ====================== */
.bottom-sheet-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 40;
  animation: fadeIn 0.3s ease-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.bottom-sheet {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: white;
  border-radius: 24px 24px 0 0;
  z-index: 50;
  max-height: 85vh;
  transform: translateY(100%);
  transition: transform 0.3s ease-out;
  display: flex;
  flex-direction: column;
}

.bottom-sheet.open {
  transform: translateY(0);
}

.bottom-sheet-header {
  position: sticky;

  top: 0;
  background-color: white;
  border-radius: 24px 24px 0 0;
  padding: 16px 16px;
  border-bottom: 1px solid #f3f4f6;
  z-index: 10;
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.header-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* margin-bottom: 16px; */
}

.header-top h2 {
  font-size: 20px;
  font-weight: bold;
  margin: 0;
}


.store-list-wrapper { /* 부모 요소 */
  height: calc(85vh - 200px);
  overflow: hidden;
}
.store-list {
  height: 100%;
  /* overflow-y: auto; */
  padding: 0px 16px;
  display: flex;
  flex-direction: column;
  gap: 16px;

}
.store-tabs{
  display: flex;
  gap: 10px;

}

.store-item {
  display: flex;
  align-items: center;
  gap: 12px;
  cursor: pointer;
  transition: background-color 0.2s;    
  justify-content: space-between;
}

.store-item:hover {
  background-color: #f9fafb;
}

.store-image {
  flex-shrink: 0;
  width: 80px;
  height: 80px;
  border-radius: 12px;
  overflow: hidden;
  background-color: #f3f4f6;
}

.store-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.store-info {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 4px;

  
}

.store-info h3 {
  font-size: 16px;
  font-weight: bold;
  
}

.store-hours {
  font-size: 14px;
  color: #5F616D;
}

.store-distance {
  font-size: 14px;
  color: #5F616D;
}

/* Scrollbar */
.store-list::-webkit-scrollbar {
  width: 6px;
}

.store-list::-webkit-scrollbar-track {
  background: transparent;
}

.store-list::-webkit-scrollbar-thumb {
  background: #d1d5db;
  border-radius: 3px;
}

.store-list::-webkit-scrollbar-thumb:hover {
  background: #9ca3af;
}

/* ======================
   18. ALARM PAGE
   ====================== */
.alarm-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 16px;
  border-bottom: 1px solid #e2e2e2;
}

.alarm-wrap .close-icon img {
  width: 32px;
  height: 32px;
}

/* ======================
   19. EVENT PAGE
   ====================== */
.event-list {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.event-card {
  padding: 20px 16px;
}

.event-image {
  height: 300px;
  overflow: hidden;
  border-radius: 8px;
}

.event-image img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

/* ======================
   20. MENU BUTTONS
   ====================== */
.menu-button-wrap {
  display: flex;
  gap: 8px;
  padding: 0 16px 12px;
  overflow-x: auto;
}

.menu-button-wrap button {
  padding: 4px 10px;
  border-radius: 50px;
  border: 1px solid #e5e7eb;
  background: white;
  cursor: pointer;
  font-size: 16px;
  font-weight: 500;
}

/* ======================
   21. UTILITY CLASSES
   ====================== */
.text-center {
  text-align: center;
}

.mb-1 {
  margin-bottom: 4px;
}

.mb-2 {
  margin-bottom: 8px;
}

.mb-3 {
  margin-bottom: 12px;
}

.mb-4 {
  margin-bottom: 16px;
}

.cursor-pointer {
  cursor: pointer;
}


/* src/pages/CartPage.css */
.cart-page {
  padding-bottom: 80px;
}

.store-info {
  display: flex;
  /* align-items: center; */
  /* padding: 16px; */
  border-bottom: 1px solid #f0f0f0;
}

.store-info h2 {
  font-size: 18px;
  font-weight: 600;
  margin-right: 8px;
}

.store-info .distance {
  color: #666;
  font-size: 14px;
  margin-right: auto;
}

.change-store-btn {
  padding: 6px 12px;
  border: 1px solid #ddd;
  background: white;
  border-radius: 4px;
  font-size: 14px;
}

.cart-tabs {
  display: flex;
  padding: 16px;
  gap: 8px;
}

.cart-tabs button {
  flex: 1;
  padding: 10px;
  border: 1px solid #ddd;
  background: white;
  border-radius: 8px;
  font-size: 14px;
}

.cart-tabs button.active {
  background: #1a1a1a;
  color: white;
  border-color: #1a1a1a;
}

.cart-items {
  padding: 0 16px;
}

.category-header {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 16px 0;
  font-weight: 600;
  font-size: 16px;
}

.cart-item {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 16px 0;
  border-bottom: 1px solid #f0f0f0;
  position: relative;
}

.cart-item input[type="checkbox"] {
  width: 20px;
  height: 20px;
}

.cart-item img {
  width: 80px;
  height: 80px;
