/* src/pages/LoginPage.css */
.login-page {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  background: white;
}

.login-container {
  width: 100%;
  max-width: 400px;
}

.login-title {
  font-size: 28px;
  font-weight: 700;
  margin-bottom: 40px;
  line-height: 1.4;
}

.brand-name {
  color: #ff7b54;
}

.login-form {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.input-field {
  width: 100%;
  padding: 16px;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  font-size: 15px;
  outline: none;
}

.input-field:focus {
  border-color: #ff7b54;
}

.input-field::placeholder {
  color: #999;
}

.password-field {
  position: relative;
}

.toggle-password {
  position: absolute;
  right: 16px;
  top: 50%;
  transform: translateY(-50%);
  background: none;
  border: none;
  cursor: pointer;
  padding: 4px;
}

.toggle-password img {
  width: 24px;
  height: 24px;
}

.auto-login {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 14px;
  color: #666;
  cursor: pointer;
}

.auto-login input[type="checkbox"] {
  width: 20px;
  height: 20px;
  cursor: pointer;
}

.btn-login {
  width: 100%;
  padding: 16px;
  background: #ff7b54;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  margin-top: 8px;
}

.btn-login:hover {
  background: #ff6a43;
}

.login-links {
  text-align: center;
  margin: 16px 0;
}

.login-links button {
  background: none;
  border: none;
  color: #666;
  font-size: 14px;
  text-decoration: underline;
  cursor: pointer;
}

.btn-signup {
  width: 100%;
  padding: 16px;
  background: white;
  color: #ff7b54;
  border: 2px solid #ff7b54;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
}

.btn-signup:hover {
  background: #fff5f2;
}

.social-login {
  display: flex;
  justify-content: center;
  gap: 16px;
  margin-top: 32px;
}

.social-btn {
  width: 56px;
  height: 56px;
  border-radius: 50%;
  border: 1px solid #e0e0e0;
  background: white;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.2s;
}

.social-btn:hover {
  transform: scale(1.05);
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.social-btn img {
  width: 32px;
  height: 32px;
}

.social-btn.naver {
  background: #03c75a;
}

.social-btn.kakao {
  background: #fee500;

}



.header_button{
  display: flex;
  align-items: center;
  gap: 10px;
    margin: 10px 16px;
    overflow-x: auto;
    white-space: nowrap; 
    overflow-y: hidden;

}

.header_button::-webkit-scrollbar {
  display: none; /* Chrome/Safari */
}


.header_button_item{
    flex: 0 0 auto; 
  display: flex;
  border: 1px solid #D9DBE0;
  border-radius: 50px ;
  padding: 6px 10px;
  gap: 4px;

}

.header_button_item span{
  font-size: 18px;
  font-weight: 600;
}

.item-wrap{
  display: flex;
  flex-direction: column;
  padding: 0 16px ;
  gap: 10px;
}